import React, { useState } from 'react';
import Breadcrumb from './Breadcrumb';
import ProductGallery from './ProductGallery';
import ProductInfo from './ProductInfo';
import ProductTabs from './ProductTabs';
import RelatedProducts from './RelatedProducts';
import './ProductPage.css';
import { useGlobal } from '../context/GlobalContext';
import { notify } from '../shared/Notification/Notificaiton';

const ProductPage = () => {
  const [activeTab, setActiveTab] = useState('description');
  const [quantity, setQuantity] = useState(1);
  const { dispatch,state } = useGlobal();

  const productData = {
    id:'BQTD004X5L',
    name: 'Wide Cotton Tunic Dress',
    price: 7.25,
    originalPrice: 9.95,
    rating: 5,
    reviewCount: 10,
    inStock: true,
    sku: 'BQTD004X5L',
    categories: ['T-Shirts', 'Tops', 'Mens'],
    tags: ['fashion', 't-shirt', 'Men'],
    description: `Piyoshop has brought to you the Hijab 3 Pieces Combo Pack PS23. It is a completely modern design and you feel comfortable to put on this hijab. Buy it at the best price.

In marketing a product is an object or system made available for consumer use it is anything that can be offered to a market to satisfy the desire or need of a customer. In retailing, products are merchandise, and in manufacturing, products are bought as raw materials and then sold as finished goods. A service is also regarded to as a type of product. Commodities are usually raw materials such as metals and agricultural products, but a commodity can also be anything widely available in the open market. In project management, products are the formal definition of the project deliverables that is delivering the objectives of the project.

A product can be classified as tangible or intangible. A tangible product is a physical object that can be perceived by touch such as a building, vehicle, gadget, or clothing. An intangible product is can only be perceived indirectly such as an insurance policy. Services can be broadly classified under intangible products which can be durable or non-durable. A product line is "a group of closely related, either because they function in a similar manner, are sold to the same customer groups, are marketed through the same types of outlets, or fall within given price ranges. Many range of product lines which may be unique to a single organisation or may be common across the business's industry. In 2002 the US Census compiled revenue figures for the finance and various product lines such as "accident, health and medical insurance premiums" and "income from secured consumer loans".`,
    images: [
      'https://weiboo.pixcelsthemes.com/weiboo/assets/images/products/product-filt1.jpg',
      'https://images.pexels.com/photos/1065084/pexels-photo-1065084.jpeg?auto=compress&cs=tinysrgb&w=600',
      'https://images.pexels.com/photos/1462637/pexels-photo-1462637.jpeg?auto=compress&cs=tinysrgb&w=600',
      'https://images.pexels.com/photos/1483771/pexels-photo-1483771.jpeg?auto=compress&cs=tinysrgb&w=600',
      'https://images.pexels.com/photos/1536619/pexels-photo-1536619.jpeg?auto=compress&cs=tinysrgb&w=600',
      'https://images.pexels.com/photos/1065084/pexels-photo-1065084.jpeg?auto=compress&cs=tinysrgb&w=600',
      'https://images.pexels.com/photos/1462637/pexels-photo-1462637.jpeg?auto=compress&cs=tinysrgb&w=600',
      'https://images.pexels.com/photos/1483771/pexels-photo-1483771.jpeg?auto=compress&cs=tinysrgb&w=600'
      
    ]
  };
 const addToCart = () => {
  if(state.cart.some(item => item.id === productData.id)) {
      notify("error", "Product already in cart!");
      return;
    }
    localStorage.setItem("globalState", JSON.stringify({
      ...state,
      cart: [...state.cart, { ...productData, quantity }]
    })
    )
    dispatch({ type: "ADD_TO_CART", payload: productData });
    notify("success", "Product added to cart!");

    
  };
  return (
    <div className="product-page">
      <div className="container">
        <Breadcrumb />
        
        
        <div className="product-section">
          <div className="product-content">
            <ProductGallery images={productData.images} />
            <ProductInfo 
              product={productData}
              quantity={quantity}
              setQuantity={setQuantity}
              addToCart={addToCart}
            />
          </div>
        </div>

        <ProductTabs 
          activeTab={activeTab}
          setActiveTab={setActiveTab}
          description={productData.description}
        />

        <RelatedProducts />
      </div>
    </div>
  );
};

export default ProductPage;