import React, { useEffect, useRef, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Thumbs } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/thumbs';
import './ProductGallery.css';

const useIsMobile = () => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 953);
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return isMobile;
};

const ProductGallery = ({ images }) => {
  const isMobile = useIsMobile();
  const [thumbsSwiper, setThumbsSwiper] = useState(null);

  const navigationPrevRef = useRef(null);
  const navigationNextRef = useRef(null);

  return (
    <div className="product-gallery">
      <div className="gallery-wrapper">
        {/* ✅ Main Swiper */}
        <Swiper
          key={isMobile ? 'mobile-main' : 'desktop-main'} // 🔁 Force re-render
          modules={[Navigation, Thumbs]}
          spaceBetween={0}
          navigation={{
            prevEl: navigationPrevRef.current,
            nextEl: navigationNextRef.current,
          }}
          onBeforeInit={(swiper) => {
            swiper.params.navigation.prevEl = navigationPrevRef.current;
            swiper.params.navigation.nextEl = navigationNextRef.current;
          }}
          thumbs={{ swiper: thumbsSwiper }}
          className="main-image-slider"
        >
          {images.map((image, index) => (
            <SwiperSlide key={index}>
              <div className="slide-container">
                <img src={image} alt={`Product ${index + 1}`} className="main-product-image" />
              </div>
            </SwiperSlide>
          ))}

          {/* ✅ Navigation Buttons */}
          <button ref={navigationPrevRef} className="custom-swiper-button-prev">
            &lt;
          </button>
          <button ref={navigationNextRef} className="custom-swiper-button-next">
            &gt;
          </button>
        </Swiper>

        {/* ✅ Thumbs Swiper */}
        <Swiper
          key={isMobile ? 'thumbs-mobile' : 'thumbs-desktop'} // 🔁 Force re-render
          modules={[Thumbs]}
          direction={isMobile ? 'horizontal' : 'vertical'}
          spaceBetween={10}
          slidesPerView={5}
          freeMode
          watchSlidesProgress
          onSwiper={setThumbsSwiper}
          className="thumbnail-slider"
        >
          {images.map((image, index) => (
            <SwiperSlide key={index}>
              <div className="thumbnail">
                <img src={image} alt={`Thumbnail ${index + 1}`} className="thumbnail-image" />
              </div>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    </div>
  );
};

export default ProductGallery;
